<template>
  <div >
      <!-- 筛选区 -->
      <el-row type="flex" class="serach-box">
        <el-col :span="5">  
          样品号：<el-input v-model="userQuery.sampleNubmber" clearable placeholder="请输入内容">
            <el-button @click="query" slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
        <el-col :span="5">
          业务员：<el-input v-model="userQuery.salesMan" clearable placeholder="请输入内容">
            <el-button @click="query" slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
        <el-col :span="5">
          客户：<el-input v-model="userQuery.customer" clearable placeholder="请输入内容">
            <el-button @click="query" slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!-- 表格区 -->
      <el-table  border :data="tableData">
        <el-table-column type="index"  label="序号" fixed width="80"></el-table-column>
        <el-table-column
          :fixed="item.isFix || false"
          :label="item.label"
          :prop="item.prop"
          :width="item.width"
          v-for="item in tableFirstList"
          :key="item.index"
        >
        </el-table-column>
        <el-table-column
          :fixed="item.isFix || false"
          :label="item.label"
          :prop="item.prop"
          :width="item.width"
          v-for="item in tableSecondList"
          :key="item.index"
        >
        </el-table-column>
      </el-table>
  </div>
</template>

<script>
import {GetAllSamplanSummary} from "network/summary.js"

export default {
  data() {
    return {
      tableData: [
      ],
      userQuery:{
        salesMan:"",
        customer:"",
        sampleNubmber:""
      },
       tableFirstList: [
        { label: "客户", prop: "customer", width: "100", isFix: "left" },
        { label: "业务员", prop: "salesMan", width: "100", isFix: "left" },
        { label: "样品号", prop: "sampleNubmber" },
        { label: "品名", prop: "sampleName" }
      ], 
       tableSecondList:[
        { label: "要求机种", prop: "machineType", width: "100"},
        { label: "要求针数", prop: "needleCount", width: "100"},
        { label: "要求用料{表纱(表纱混率)+里纱}", prop: "materialsRequired" , width: "150"},
        { label: "要求尺寸(脚底*上统)", prop: "requiredZize" , width: "180"},
        { label: "样品数量", prop: "sampleCount" , width: "100" },
        { label: "颜色(主色)", prop: "mainColor" , width: "100"},
        { label: "功能性、缝头方式、后道备注", prop: "featureSewingTypePostProcessComment", width: "150" },
        { label: "企划克重", prop: "planWeight" },
        { label: "注意点", prop: "detectRequest" , width: "100"},
        { label: "希望交期", prop: "planDeliveryTime" , width: "100"},
        { label: "样品交期", prop: "sampleDeliveryTime" },
        { label: "样品尺寸(脚底*上统)", prop: "customer" },
        { label: "样品克重", prop: "sampleWight" },
        { label: "样品寄出数量", prop: "sampleSendNumber" },
       ]
    };
  },
  created(){
    this.inital();
  },
  methods:{
  //初始化
  inital(){
    this.getSummaryList(this.userQuery);
  },
  //获得汇总单列表
  getSummaryList(data){
    GetAllSamplanSummary(data).then(res=>{
      if(res.success){
        this.tableData = res.result.samplePlansSummary.items;
        this.$message.success("获得汇总单数据成功！")
      }
    })
  },
  //根据筛选条件查询
  query(){
    this.getSummaryList(this.userQuery);
  }
  }
};
</script>

<style scoped>
.el-row{
  margin-bottom: 10px;
}
.el-input-group {
  width: 65% !important;
}
</style>